.experience{
    min-height: 100vh;
}

.experience-body {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.experience-description {
    width: 100%;
    flex: 0.65;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    margin-right: 2rem;
}

.experience-description > h1 {
    font-size: 3.5rem;
    font-family: var(--primaryFont);
    margin-bottom: 2rem;
}

.experience-card {
    display: flex;
    flex-direction: row;
    align-items:flex-start;
    justify-content: flex-start;
    width: 90%;
    height: 140px;
    padding: 1.5rem;
    border-radius: 20px;
    margin-bottom: 1.5rem;
    transition: background-color 200ms ease-in-out;
    position: relative;
}

.expcard-img {
    border-radius: 50%;
    width: 55px;
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
}

.expcard-img > img {
    width: 36px;
    pointer-events: none;
}

.experience-details {
    margin-left: 0.6rem;
}

.experience-details > h6 {
    font-family: var(--primaryFont);
    font-size: 0.85rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
}

.experience-details > h4 {
    font-family: var(--primaryFont);
    font-size: 1.225rem;
    font-weight: 600;
}

.experience-details > h5 {
    font-family: var(--primaryFont);
    font-size: 1.15rem;
    font-weight: 600;
}

.experience-location {
    position: absolute;
    bottom: 1rem;
    right: 1.5rem;
    font-family: var(--primaryFont);
    font-size: 0.9rem;
    font-weight: 500;
}

.description-dialog {
    position: absolute;
    top: 0;
    right: 100%;
    width: 350px;
    z-index: 1000;
    padding: 1.5rem;
    border-radius: 15px;
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
    margin-right: 1rem;
    animation: slideInLeftHighlight 0.4s ease-out;
    max-height: 300px;
}

.description-content {
    max-height: 250px;
    overflow-y: auto;
    padding-right: 0.5rem;
}

.description-content::-webkit-scrollbar {
    width: 6px;
}

.description-content::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.2);
    border-radius: 3px;
}

.description-content::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.8);
    border-radius: 3px;
}

.description-content::-webkit-scrollbar-thumb:hover {
    background: rgba(255, 255, 255, 1);
}

.description-dialog p {
    margin: 0;
    font-family: var(--primaryFont);
    font-size: 0.95rem;
    line-height: 1.5;
    white-space: pre-line;
}

@keyframes slideInLeftHighlight {
    0% {
        opacity: 0;
        transform: translateX(20px) scale(0.95);
        box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
    }
    50% {
        opacity: 0.8;
        transform: translateX(-5px) scale(1.02);
        box-shadow: 0 12px 35px rgba(0, 0, 0, 0.25);
    }
    100% {
        opacity: 1;
        transform: translateX(0) scale(1);
        box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
    }
}

.experience-image {
    box-sizing: border-box;
    padding: 1rem;
    flex: 0.35;
    margin-left: 7%;
    margin-top: 1rem;
    pointer-events: none;
}

.experience-image > img {
    width: 100%;
    pointer-events: none;
}






@media (min-width: 992px) and (max-width: 1380px) {

}


@media screen and (max-width: 992px) {
    .experience {
        min-height: 100%;
    }
    .experience-image {
        display: none;
    }
    .experience-description {
        flex: 1;
        margin: auto;
    }    
    .experience-description > h1{
        font-size: 3.2rem;
    }
    .experience-card {
        width: 100%;
        padding: 1.5rem;
        margin-bottom: 1.5rem;
    }
}

@media screen and (max-width: 800px) {
    .experience-description > h1{
        font-size: 3rem;
        margin-bottom: 2rem;
        align-self: center;
    }
    .experience-description {
        padding: 1rem;
    }
    .experience-card {
        padding: 1rem;
        margin-bottom: 1.5rem;
        height: 130px;
    }
    .expcard-img {
        border-radius: 50%;
        width: 45px;
        height: 45px;
    }
    
    .expcard-img > img {
        width: 30px;
    }
    .experience-details > h6 {
        font-size: 0.85rem;
        margin-bottom: 0.45rem;
    }
    
    .experience-details > h4 {
        font-size: 1.125rem;
        line-height: 126%;
    }
    
    .experience-details > h5 {
        font-size: 1.05rem;
        line-height: 126%;
    }
    
    .experience-location {
        font-size: 0.8rem;
        bottom: 0.8rem;
        right: 1rem;
    }
}

@media screen and (max-width: 600px) {
    .experience {
        padding: 1rem;
    }
    
    .experience-description > h1{
        font-size: 2.2rem;
        text-align: center;
        margin-bottom: 1.5rem;
    }
    
    .experience-description {
        padding: 0.5rem;
        margin-right: 0;
    }
    
    .experience-card {
        width: 100%;
        padding: 1rem;
        margin-bottom: 1rem;
        min-height: 120px;
    }
    
    .expcard-img {
        width: 40px;
        height: 40px;
    }
    
    .expcard-img > img {
        width: 25px;
    }
    
    .experience-details > h6 {
        font-size: 0.8rem;
    }
    
    .experience-details > h4 {
        font-size: 1rem;
    }
    
    .experience-details > h5 {
        font-size: 0.95rem;
    }
}

